import React, { useCallback } from 'react';
import Page from 'components/Layout/Page';
import { Container, Grid } from '@material-ui/core';
import { useDeployCommits } from 'hooks/useDeployCommits';
import DeploymentList from 'components/List/DeploymentList';

const DeploymentPage = () => {
  const { deployments, head, deploy } = useDeployCommits();
  const handleDeployButtonClick = useCallback(
    (runId: number) => {
      deploy(runId);
    },
    [deploy]
  );
  return (
    <Page pageName="Deployments" showTitle={true} description="루비콘 홈 배포 관리 센터">
      <Container>
        <Grid container justify="center">
          <Grid item xs={12} lg={8}>
            <DeploymentList>
              {deployments.map((deployment, index) => (
                <DeploymentList.Item
                  key={index}
                  runId={deployment.id}
                  message={deployment.head_commit.message}
                  date={deployment.head_commit.timestamp}
                  sha={deployment.head_sha}
                  url={deployment.html_url}
                  isDeployed={deployment.head_sha === head}
                  underline={index < deployments.length - 1}
                  onClick={handleDeployButtonClick}
                />
              ))}
            </DeploymentList>
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default DeploymentPage;
