import Github from 'utils/github';
import { GetResponseDataTypeFromEndpointMethod } from '@octokit/types';
import { useState, useCallback, useEffect } from 'react';

const defaultOption = {
  owner: 'Lubycon',
  repo: 'lubycon-home',
};

type Runs = GetResponseDataTypeFromEndpointMethod<typeof Github.actions.listWorkflowRuns>;

export function useDeployCommits() {
  const [runs, setRuns] = useState<Runs['workflow_runs']>([]);
  const [head, setHead] = useState('');

  const fetchDeployRuns = useCallback(async () => {
    const [
      {
        data: { workflows },
      },
      {
        data: { workflow_runs: runs },
      },
    ] = await Promise.all([
      Github.actions.listRepoWorkflows({
        ...defaultOption,
      }),
      Github.actions.listWorkflowRunsForRepo({
        ...defaultOption,
      }),
    ]);

    const deployWorkflow = workflows.find(w => w.name === 'Deploy');
    return runs.filter(run => run.workflow_id === deployWorkflow.id);
  }, []);

  const fetchMasterBranch = useCallback(async () => {
    const { data } = await Github.repos.getBranch({
      ...defaultOption,
      branch: 'master',
    });
    return data;
  }, []);

  const deploy = useCallback(async (runId: number) => {
    try {
      await Github.actions.reRunWorkflow({
        ...defaultOption,
        run_id: runId,
      });
    } catch (e) {
      return;
    }
  }, []);

  useEffect(() => {
    (async () => {
      const [master, runs] = await Promise.all([fetchMasterBranch(), fetchDeployRuns()]);
      const lastCommit = master.commit;
      setHead(lastCommit.sha);
      setRuns(runs);
    })();
  }, [fetchDeployRuns, fetchMasterBranch]);

  return {
    deployments: runs,
    head,
    deploy,
  };
}
